.card {
  background-color: var(--white);
  padding: 3rem 2.4rem;
  position: relative;
  top: -7.4rem;
  border-radius: 0.8rem;
  box-shadow: 1px 1px 10px 0px rgba(0, 0, 0, 0.17);
}

.card h1 {
  font-size: 2.4rem;
}

.title {
  color: var(--marine-blue);
  margin-bottom: 1.9rem;
}

.description {
  color: var(--cool-gray);
  margin-bottom: 2.7rem;
}

@media screen and (min-width: 942px) {
  .card {
    position: static;
    box-shadow: none;
    padding: 0;
    width: 45.2rem;
  }

  .card h1 {
    margin-top: 4.6rem;
    font-size: 3.2rem;
  }
}
